.table_survey_results {
  table-layout: fixed;
  width: 100%;
}
.table_survey_results td, .table_survey_results th {
  vertical-align: top;
  border-top: 1px solid #ccc;
  padding:10px;
  width: 250px;
  height: 75px;
}
.table_survey_results th {
  position:absolute;
  left:0;
  min-width:400px;
}
.outer {
  position:relative;
}
.inner {
  overflow-x:scroll;
  overflow-y:visible;
  width: 750px;
  margin-left: 400px;
}

.panel-fullheight {
  height: 415px;
}